export type IPMessage = {
    id: string | number;
    action: string;
    payload: string | number | object | any;
    created: Date | null;
    status?: number;
}

export type IPMessageProxyCallPayload = {
    url: string;
    data: object | string | any;
    headers: object | null;
    method: string;
}

export type MessageHandler = (msg: IPMessage, sendReplyMsg: (m: IPMessage) => void) => void;

export const IPM_ACTION_READY = "IPM::ACTION::READY";
export const IPM_ACTION_CONNECT = "IPM::ACTION::CONNECT";
export const IPM_ACTION_MESSAGE_RECEIVED = "IPM::ACTION::MESSAGE_RECEIVED";

export default class IPM {

    name: string = 'default'
    port: MessagePort;
    messageHandlers: {[k: string]: Array<MessageHandler>}

    constructor(port: MessagePort, name: string = 'default') {
        this.name = name;
        this.port = port;
        this.messageHandlers = {};
        this.port.onmessage = this.onMessage.bind(this);
    }

    public on(action: string, handler: MessageHandler) {
        if (!this.messageHandlers.hasOwnProperty(action)) {
            this.messageHandlers[action] = [];    
        }

        this.messageHandlers[action].push(handler);    
    }

    public off(action: string, handler?: MessageHandler) {
        if (this.messageHandlers.hasOwnProperty(action) && undefined === handler) {
            this.messageHandlers[action] = [];    
        } else if (this.messageHandlers.hasOwnProperty(action)) {
            this.messageHandlers[action] = this.messageHandlers[action].filter(handlerFn => handlerFn !== handler);
        } 
    }

    public send(msg: IPMessage) {
        this.port.postMessage(JSON.stringify(msg));
    }

    public destroy() {
        this.messageHandlers = {};
    }

    protected onMessage(e: MessageEvent) {
        try {
            const msg = JSON.parse(e.data) as IPMessage;
            this.trigger(msg.action, msg);
        } catch(e) {
            console.log(this.name + "On message throw", e);
            return;
        }
    }

    protected trigger(action: string, msg: IPMessage) {
        //handle the possible IPM_ACTION_MESSAGE_RECEIVED hooks for any message
        if (this.messageHandlers.hasOwnProperty(IPM_ACTION_MESSAGE_RECEIVED) && this.messageHandlers[IPM_ACTION_MESSAGE_RECEIVED].length) {
            this.messageHandlers[IPM_ACTION_MESSAGE_RECEIVED].forEach(
                handler => {
                    handler(msg, (m:IPMessage) => this.send(m));
                }
            );
        }

        //handle the action hooks
        if (this.messageHandlers.hasOwnProperty(action) && this.messageHandlers[action].length) {
            this.messageHandlers[action].forEach(
                handler => {
                    handler(msg, (m:IPMessage) => this.send(m));
                }
            );
        }
    }
}