import Client from "opsapi";

(new Client('http://localhost:3000'))
.init()
.then(
  sdk => {

      sdk.getUserName().then(
        name => document.querySelector('#name').innerText = `Hello, ${name.payload}`
      );

      sdk.getAccessToken().then(
        token => document.querySelector('#token').innerText = `${token.payload}`
      );

      document.querySelector('#getBrew').addEventListener('click', () => {
        document.querySelector('#getBrew').innerText = '....';
          sdk.call('https://api.openbrewerydb.org/breweries/' + (Math.ceil(Math.random()*300)), null, {}, 'GET').then(
              msg => {
                  const brew = msg.payload.data;
                  document.querySelector('#brew').innerText =  brew.name  + ' ' + brew.street + ',' + brew.city + ' ' + brew.state + '' + brew.country;
              }
          ).finally(
            () => document.querySelector('#getBrew').innerText = 'Get Brewery (make call trough sdk)'
          );
      });
  }
).catch((e) => console.log("client connect send failed",e));
