import uuidv4 from 'uuid';
import IPM, { IPM_ACTION_CONNECT, IPMessage, IPM_ACTION_MESSAGE_RECEIVED, IPM_ACTION_READY } from './ipm/ipm';

const TIMEOUT_LIMIT = 15000;

export default class Client {
    ipm: IPM;
    channel: MessageChannel;
    port: MessagePort;
    portalUrl: string;
    promiseQueue: {[k: string]: {resolve: Function, reject: Function}};

    constructor(portalUrl: string) {
        this.portalUrl = portalUrl;
        this.promiseQueue = {};
        this.channel = new MessageChannel();
        this.port = this.channel.port1;
        this.ipm = new IPM(this.port, 'CLIENT');
    }

    init(): Promise<any> {
        console.log("SENDING CONNECT REQ FROM CLIENT");
        this.ipm.on(IPM_ACTION_MESSAGE_RECEIVED, this.onMessageReturn.bind(this));
        return new Promise((resolve, reject) => {
            this.ipm.on(IPM_ACTION_READY, () => resolve(this.getSdk()));
            window.top.postMessage(JSON.stringify({action: IPM_ACTION_CONNECT, id: 999}), this.portalUrl, [this.channel.port2]);
            setTimeout(() => reject("CLIENT TIMED OUT after 15 secs!"), 15000);
        });
    }

    onMessageReturn(msg: IPMessage) {
        if (this.promiseQueue.hasOwnProperty(msg.id)) {
            this.promiseQueue[msg.id].resolve(msg);
            delete this.promiseQueue[msg.id];
        }
    }

    send(action: string, payload: any = null): Promise<any> {
        const id = uuidv4();
        const message = {
            id,
            action,
            payload,
            created: new Date()
        };

        return new Promise((resolveFn, rejectFn) => {
            this.promiseQueue[id] = {
                resolve: resolveFn,
                reject: rejectFn
            };

            this.port.postMessage(JSON.stringify(message));
            setTimeout(() => rejectFn(new Error('OPS CLIENT TIMED OUT FOR MSG ID: ' + id )), TIMEOUT_LIMIT);
        })
    }

    getSdk() {
        return {
            getUserName: () => this.send('getUserName'),
            getUser: () => this.send('getUser'),
            getPlugins: () => this.send('getPlugins'),
            getAccessToken: () => this.send('getAccessToken'),
            call: (url: string, data: any, headers: object, method: string) => {
                const message = {
                    url,
                    data,
                    headers: headers || {},
                    method: method || "GET"
                };
                return this.send('call', message);
            }
        }
    }
 }
